	onShow() {
		this.req_cartlist();
	},
	methods: {
		add(index,itemindex){
			this.list[index].mall[itemindex].num+=1;
			this.req_updateNum(index,itemindex);
		},
		del(index,itemindex){
			this.list[index].mall[itemindex].num-=1;
			if(this.list[index].mall[itemindex].num==1){
				this.list[index].mall.splice(itemindex,1)
				if(!this.list[index].mall.length){
					this.list.splice(index,1)
				}
			}
			this.req_updateNum(index,itemindex);
		},
		//购物车商品数量更新
		req_updateNum(index,itemindex){
			return;
			ut.request({
				url: "cart/updateNum",
				data:{
					cartid:this.list[index].mall[itemindex].id,
					num:this.list[index].mall[itemindex].num
				}
			}).then(data=>{
				//this.list=data
			})
		},
		//删除购物车商品
		req_delete(cartid){
			return;
			ut.request({
				url: "cart/delete",
				data:{
					cartid:cartid
				}
			}).then(data=>{
				//this.list=data
			})
		},
		req_cartlist(){
			ut.request({
				url: "cart/list",
			}).then(data=>{
				//this.list=data
			})
		}
	}
}



